.site-header {
    height: auto;
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 5rem;
    align-items: center;
    padding: 1rem 3rem;
    /*border-bottom-width: 4px;
    border-bottom-style: solid;
    border-image: var(--gradientColor) 1;*/
}

.title{
    text-decoration: none;
    color: inherit;
}

.searchInput{
    padding: 0.4rem 1rem;
    margin-right: 0.4rem;
    border: 1px solid black;
    border-radius: 3rem;
    transition: border 0.5s ease-in;
    border: none;
    box-shadow: 2px 7px 13px 0px rgb(0 0 0 / 75%);
}

.searchInput:focus{
    outline: none;
}

.searchBtn{
    padding: 0.3rem 0.3rem;
    border-radius: 50%;
    border: 1px solid black;
    cursor: pointer;
    transition: transform .2s ease-in;
    box-shadow: 2px 7px 13px 0px rgb(0 0 0 / 75%);
}

.searchBtn:hover{
    transform: scale(1.2);
    background: var(--gradientColor);
}

.errMsg{
    margin-right: 0.5rem;
    font-size: 1.2rem;
    font-weight: bolder;
    color: red;
    opacity: 0;
    transition: opacity 0.5s ease-in;
    z-index: -10;
}

.errMsgShow{
    opacity: 1;
}

.errInput{
    outline: 2px solid red;
}

@media(max-width:600px) {
    .site-header{
        height: auto;
        flex-direction: column;
        gap: 1rem;
        padding: 1rem 1rem;
    }

    .site-header h1{
        font-size: 2.6rem;
    }

}